import axios from '@/utils/uniAxios.js';
export default {
	components:{
	},
	data() {
		return {
			addressData: {
			 "addressLabel": "家庭",//地址标签，默认家庭
			  "city": "",//城市
			  "defaultStatus": 1,//是否默认,0否，1是
			  "detailAddress": "",//详细地址
			  "id": '',
			  "memberId": 0,//会员ID
			  "name": "",//收货人姓名
			  "phoneNumber": "",//电话
			  "postCode": "",//邮编
			  "province": "",//省份
			  "region": ""//区
			},
			address:'',
			addressLabels:['家庭','公司','学校'],
			n:0,
			defaultStatus:true
		}
	},
	onLoad(option){
		let title = '添加收货地址';
		if(option.type==='edit'){
			title = '编辑收货地址'
			this.addressData = JSON.parse(option.data)
			console.log(this.addressData)
		}
		this.manageType = option.type;
		uni.setNavigationBarTitle({
			title
		})
	},
	methods: {
		// 地址标签
		changeList(item,index){
			this.n = index;//this指向app
			this.addressData.addressLabel = item;
		},
		// 切换默认
		switchChange(e){
			this.defaultStatus = e.detail;
			if(this.defaultStatus){
				this.addressData.defaultStatus = 1
			}else{
				this.addressData.defaultStatus = 0
			}
		},
		
		//地图选择地址
		chooseLocation(){
			uni.getLocation({
			    type: 'wgs84',
				geocode:true,
			    success: function (res) {
					console.log(res)
					// 在小程序中不能获取到address的详细信息。需要根据经纬度逆地理编码
					//https://ask.dcloud.net.cn/article/35070
					// https://lbs.qq.com/qqmap_wx_jssdk/
					
			    }
			});
			
			uni.chooseLocation({
				success: (data)=> {
					this.address = data.name;
				}
			})
		},
		
		//提交
		confirm(){
			let data = this.addressData;
			if(!data.name){
				this.$api.msg('请填写收货人姓名');
				return;
			}
			if(!/^(1[0-9])\d{9}$/.test(data.phoneNumber)){
				this.$api.msg('请输入正确的手机号码');
				return;
			}
			if(!this.address){
				this.$api.msg('请在地图选择所在位置');
				return;
			}
			if(!data.detailAddress){
				this.$api.msg('请填写门牌号信息');
				return;
			}
			console.log(this.addressData)
			axios.post('/member/address/add',this.addressData).then(res=>{
				if(res.data.code==200){
					//this.$api.prePage()获取上一页实例，可直接调用上页所有数据和方法，在App.vue定义
					this.$api.prePage().refreshList(data, this.manageType);
					this.$api.msg(`地址${this.manageType=='edit' ? '修改': '添加'}成功`);
					setTimeout(()=>{
						uni.navigateBack()
					}, 800)
				}
				
			})
			
		},
	}
}
